@import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Noto Sans JP', sans-serif;
}

#navi{
    width: 100vw;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.header{
    margin: 1rem;

}

.nav{
    width: 40%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    list-style-type: none;
}

.nav li {
    margin: auto 0;
    list-style-type: none;
}

#search input{
    width: 10vw;
    padding: 0.5rem 1rem;
    border-radius: 0.7rem;
    transition: 0.5s cubic-bezier(.29,.62,0,1.04);
}

#search input:focus{
    border:none;
    width: 12vw;

}